import React from 'react';


export default function HoverButton(props) {
    return(
        <React.Fragment>
            
            <d className="hover-box" id={props.key} onClick={props.onClickSideNav} >

                <img className="hover-item" src={props.pathSVG} draggable="false" href="#" width="30" height="30" alt=""/>
                <d className="hover-item" href="#" >
                {props.text}
                </d>
            </d>
           
        </React.Fragment>





    )


}