import React from 'react'


export default function SideNavCard(props) {
    return (
    
    <React.Fragment>

    <div id="sidenav-card" className="position-relative">
        <img className="sidenav-img border" src={props.img} />
        <div id="item-qtde">
            <span>1</span>
        </div>
        <div id="item-title">
            <span>Calabresa</span>
        </div>
        <div id="sidenav-subtitle">
            <span>nibh praesent tristique magna sit amet</span>
        </div>
        <svg class="sidenav-button-minus">
            <rect id="sidenav-button-minus" rx="11" ry="11" x="0" y="0" width="23.198" height="22.45">
            </rect>
        </svg>
        <svg class="sidenav-button-plus">
            <rect id="sidenav-button-plus" rx="11" ry="11" x="0" y="0" width="23.198" height="22.45">
            </rect>
        </svg>
        <div id="sidenav-price">
            <span>R$ 19,90</span>
        </div>
    </div>

    </React.Fragment>

    )
}