export default function toogleSidenav() {
    let sacolaHover = document.getElementsByClassName("hover-box")[1];
    let sideNav = document.getElementsByClassName("sidenav-container")[0];
    let VisibleSidenav;

    console.log("Sidebar Opacity: ",sideNav.style.opacity);
    console.log("Sacola Opacity: ", sacolaHover.style.opacity) 


    if (sacolaHover.style.opacity == "")
    {
        sacolaHover.style.opacity = 1;
    }



    if (sacolaHover.style.opacity == 1 ){
        VisibleSidenav = true;
    }

    else{
        VisibleSidenav = false;
    }



    if (VisibleSidenav){
        sacolaHover.style.opacity = 0;
        sideNav.style.opacity = 1;

        sacolaHover.style.visibility = "hidden"
        sideNav.style.visibility = "visible"
        
    }
    else{
        sacolaHover.style.opacity = 1;
        sideNav.style.opacity = 0;
        
        sideNav.style.visibility = "hidden"
        sacolaHover.style.visibility = "visible"
    }
    

}
