import React from 'react'
import './index.css'



export default function Card(props){

    return(
        <React.Fragment>

        <div id="card" className="p-3 m-2 rounded float-sm-left css-selector" >
            <div id="card-title">
                <span>{props.name}</span>
            </div>
            <div id="card-subtitle">
                <span className="text-wrap text-truncate" > aliquam ultrices sagittis orci a scelerisque purus</span>
            </div>
	        
            <img id="img" src={props.img} draggable="false"/>
		
	        <div id="price">
		        <span>R$ {props.price}</span>
	        </div>
	    <svg class="heart" fill="#fff" viewBox="0 35.525 31.24 25.576">
		<path id="heart" d="M 22.74290466308594 35.52503967285156 C 27.42635536193848 35.5478401184082 31.21748924255371 39.33889389038086 31.24029159545898 44.02242660522461 C 31.24029159545898 52.60305023193359 15.62018489837646 61.10043334960938 15.62018489837646 61.10043334960938 C 15.62018489837646 61.10043334960938 0 52.47811126708984 0 44.02242660522461 C 0 39.32949066162109 3.804370403289795 35.52503967285156 8.497386932373047 35.52503967285156 L 8.497386932373047 35.52503967285156 C 11.37013816833496 35.50263595581055 14.05584907531738 36.94754028320313 15.62018871307373 39.35715484619141 C 17.1952075958252 36.95925903320313 19.87398147583008 35.51810073852539 22.74290466308594 35.52503967285156 Z">
		</path>
	</svg>
        </div>

    </React.Fragment>
    )



}