import React from 'react';
import ListRestaurant from './ListRestaurant'
import MakePizza from './MakePizza'
import './index.css'
import HeadingText from "./HeadingText";
import Categories from './Categories';
import SVGiconPizza from '../../images/iconPizza.svg'
import SideNav from './SideNav';

import toogleSidenav from '../../toogleSideNav'

export default function Body(){


    return(
        <React.Fragment>
       
        <SideNav onClickSideNav={toogleSidenav}></SideNav>
       
        <div className="corpo container">
      
        
        <div className= {window.screen.width < 769 ? " center d-flex flex-column flex-nowrap" : "center"}>

        
        <MakePizza></MakePizza>
        <HeadingText content = {"explore as categorias"} sticky={false} ></HeadingText>
        <div className="container corpo d-flex flex-wrap justify-content-around">
            <Categories path={SVGiconPizza} text="Pizza"></Categories>
            <Categories path={SVGiconPizza} text="asda"></Categories>
            <Categories path={SVGiconPizza} text="Pizadasza"></Categories>
            <Categories path={SVGiconPizza} text="Piasdaszza"></Categories>
            <Categories path={SVGiconPizza} text="adaPizza"></Categories>
        </div>

        <HeadingText content = {"os mais populares"} sticky={true}></HeadingText>

        <ListRestaurant></ListRestaurant>
        
        </div>
        </div>
        
        </React.Fragment>

    )

}