import React from 'react'
import './index.css'
import  pizza from '../../../images/iconPizza.svg'



export default function Categories(props){
    return (
        <React.Fragment>

<div id="circle-container">
	<svg class="circle-green">
		<rect id="circle-green" rx="50" ry="50" x="0" y="0" width="70" height="70">
		</rect>
	</svg>
	<img class="icon" src={props.path} draggable="false" />

	<svg class="circle-white">
		<ellipse id="circle-white" rx="29.625" ry="29.625" cx="29.625" cy="29.625">
		</ellipse>
	</svg>
	<div className="name text-center" draggable="false">
			{props.text}
	</div>
</div>	


        </React.Fragment>
    )

}