import React from 'react'
import '../MakePizza/index.css'
import MakeSVG from '../../../images/makepizza.svg'


export default function MakePizza(){

    return(

            <React.Fragment>
                    <div className="container d-flex justify-content-center">
                    <img className="MakePizza heartbeat" src={MakeSVG} draggable="false" href="#"/>
                    </div>

            </React.Fragment>
        
    )
}