import React from 'react';
import  '../../../node_modules/bootstrap/dist/css/bootstrap.min.css'
import '../Header/index.css'

import HoverButton from './HoverButton'
import userSVG from '../../images/user.svg'
import bagSVG from '../../images/bag.svg'
import pinSVG from '../../images/pin.svg'
import logoSVG from '../../images/pizzalogo.svg'


import toogleSidenav from '../../toogleSideNav'

export default function Header() {
    return(
    <React.Fragment>

    <header>
        
    <nav class="navbar">
        <b className="logo" href="/">
        <img className="hover-item p-0 m-0" src={logoSVG} draggable="false" href="#" width="70" height="70" alt=""/> 
        </b>
        <b>
            <input type="text" class="form-control nav-item" id="exampleFormControlInput1" placeholder="Busque pelo cardápio"/>
        </b>

        <a>
            <div className="pin">
            <img className="hover-item" src={pinSVG} draggable="false" href="#" width="25" height="25" alt=""/>
            R. Um, 196
            </div>
        </a>

        <d className="hover-holder nav-item d-flex justify-content-around icon-box">
            <HoverButton pathSVG={userSVG} text="Perfil" key="perfil"></HoverButton>
            <HoverButton pathSVG={bagSVG} text="Sacola" key="sacola" onClickSideNav={toogleSidenav}></HoverButton>
        </d>

    </nav>

    </header>
    </React.Fragment>
    )
}